@import "../../styles/mixins";

.checkout-page {
    text-align: start;
    margin-bottom: 140px;

    .go-back {
        text-align: start;
        opacity: 0.5;
        margin: 38px 0 80px;
        cursor: pointer;

        @include for-pre-tablet-layout {
            margin: 38px 0 24px;
        }
    }

    .checkout {
        @include for-pre-tablet-layout {
            flex-direction: column;
        }
    }

    .checkout-form {
        background-color: white;
        padding: 48px;
        border: 0.5rem;
        margin-right: 30px;
        flex: 1;

        @include for-pre-tablet-layout {
            margin-right: unset;
            margin-bottom: 32px;
        }

        h3 {
            margin-bottom: 40px;
        }

        &-title {
            text-transform: uppercase;
            font-weight: 700;
            font-size: 13px;
            line-height: 25px;
            letter-spacing: 1px;
            color: #d87d4a;
            margin-bottom: 16px;
        }

        .billing-details-container {
            margin-bottom: 50px;

            & > * {
                flex: 1 1 33%;
            }

            & > *:first-child {
                margin-bottom: 24px;
                margin-right: 16px;

                @include for-pre-tablet-layout {
                    margin-right: 0;
                }
            }

            @include for-pre-tablet-layout {
                & > *:not(:last-child) {
                    margin-bottom: 24px;
                }
            }
        }

        .shipping-info-container {
            margin-bottom: 60px;

            & > *:first-child {
                flex: 1 1 100%;
                margin-bottom: 24px;
            }

            & > *:not(:first-child) {
                flex: 1 1 48%;
            }

            // & > *:first-child {
            //     flex-basis: 100%;
            //     margin-bottom: 24px;
            // }

            & > *:nth-child(2) {
                margin-right: 16px;
                margin-bottom: 24px;

                @include for-pre-tablet-layout {
                    margin-right: 0;
                }
            }

            // & > *:nth-child(3) {
            //     margin-bottom: 24px;
            // }

            // & > *:nth-child(2),
            // & > *:nth-child(3) {
            //     flex: 1;
            // }

            @include for-pre-tablet-layout {
                & > *:not(:last-child) {
                    margin-bottom: 24px;
                }
            }
        }

        .payment-methods {
            font-weight: 700;
            font-size: 12px;
            line-height: 1rem;

            @include for-pre-tablet-layout {
                margin-bottom: 16px;
            }
        }

        .radio-container {
            margin-bottom: 24px;

            & > *:first-child {
                margin-bottom: 16px;
            }

            @include for-pre-tablet-layout {
                align-items: flex-start;
            }
        }

        .payment-details-inputs-container {
            & > * {
                flex: 1;
            }
            & > *:first-child {
                margin-right: 16px;
                @include for-pre-tablet-layout {
                    margin-right: unset;
                    margin-bottom: 16px;
                }
            }

            @include for-pre-tablet-layout {
                flex-direction: column;
            }
        }
    }

    .summary {
        padding: 30px;
        width: 350px;
        background-color: white;
        height: 100%;

        @include for-pre-tablet-layout{
            width: 100%;
        }

        .summary-title {
            margin-bottom: 30px;
        }

        .total-price {
            color: #d87d4a;
        }

        .checkout-txt {
            opacity: 0.5;
            margin-bottom: 8px;
            text-transform: uppercase;

            &:nth-of-type(3) {
                margin-bottom: 24px;
            }

            &:last-of-type {
                margin-bottom: 32px;
            }
        }

        .items {
            margin-bottom: 32px;

            & > *:not(:last-child) {
                margin-bottom: 24px;
            }
        }
    }
}
